/* src/components/Calculator.css */
.calculator {
    width: 400px;
    margin: auto;
    background-color: #333;
    border-radius: 10px;
    padding: 20px;
  }
  
  .display {
    background-color: #444;
    color: white;
    font-size: 2em;
    text-align: right;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10px;
  }
  
  .button {
    background-color: #666;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1.5em;
    padding: 20px;
    text-align: center;
  }
  
  .button.orange {
    background-color: orange;
  }
  